.gpt3__cta{
    background: var(--gradient-bar);
    border-radius: 8px;
    padding: 2rem;
    margin: 4rem ;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: var(--font-family);
}
.gpt3__cta-content{
    
    display: flex;
    flex-direction: column;
    text-align: left;
    
}

.gpt3__cta-content p {
    font-size: 12px;
    line-height: 30px;
    font-weight: 500;
    color: #0e0e0e;
}

.gpt3__cta-content h3 {
    font-size: 24px;
    line-height: 45px;
    font-weight: 800;
}

.gpt3__cta-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}

.gpt3__cta-btn button{
    color: #fff;
    background: #000;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 18px;
    line-height: 30px;
    padding: 0.5rem 1rem;
    border-radius: 2rem;
    border: none;
    outline: none;
    cursor: pointer;
    min-width: 150px;
}

@media screen and (max-width:650px ){
    .gpt3__cta{
        flex-direction: column;
    }
    .gpt3__cta-btn{
        margin: 2rem 0 0 ;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__cta{
        margin: 4rem 2rem;
    }
    .gpt3__cta-content h3{
        font-size: 18px;
        line-height: 32px;

    }
    .gpt3__cta-btn button{
        font-size: 14px;
        line-height: 28px
    }
}